import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import {
    BrowserRouter as Router, Routes, Route
} from 'react-router-dom';

import Home from './containers/Home/home';
import AboutMe from "./containers/AboutMe/AboutMe";
import Works from "./containers/Works/works";
import Biography from "./containers/Biography/biography";
import Skills from "./containers/Skills/skills";
import Timeline from "./containers/Timeline/timeline";
import Error from "./containers/404/404";
import FiverrWorks from "./containers/FiverrWorks/fiverrWorks";
import UniversityWorks from "./containers/UniversityWorks/universityWorks";
import Market from "./containers/FiverrWorks/works/market";

ReactDOM.render(
    <React.StrictMode>
        <Router>
            <Routes>
                <Route path='/' exact element={<Home/>}/>
                <Route path='/about-me' exact element={<AboutMe/>}/>
                <Route path='/about-me/biography' exact element={<Biography/>}/>
                <Route path='/about-me/skills' exact element={<Skills/>}/>
                <Route path='/about-me/timeline' exact element={<Timeline/>}/>
                <Route path='/works' exact element={<Works/>}/>
                <Route path='/works/fiverr' exact element={<FiverrWorks/>}/>
                <Route path='/works/fiverr/market' exact element={<Market/>}/>
                <Route path='/works/university' exact element={<UniversityWorks/>}/>
                <Route path='*' exact element={<Error/>}/>
            </Routes>
        </Router>
    </React.StrictMode>,
    document.getElementById('root')
);
