import '../../styles/timeline.css'
import React from "react";
import Header from "../../components/Header/header";
import SpecialFooter from "../../components/SpecialFooter/SpecialFooter";
import isep from '../../images/timeLine/ISEP.png'
import fiverr from '../../images/timeLine/fiverr.svg'

function Timeline() {
    return (
        <body class="colorscheme-auto">
        <main className="wrapper">
            <Header/>

            <div className="content">
                <section className="container centered">

                    <div className="body2">
                        <div className="container-title">
                            <div className="titles">
                                <h4 className="title title-line text-capitalize mb-4">Timeline</h4>
                            </div>
                        </div>
                        <section id="cd-timeline" className="cd-container">
                            <div className="cd-timeline-block">
                                <div className="cd-timeline-img">
                                    <img className="img-timeline" src={isep} alt=""/>
                                </div>

                                <div className="cd-timeline-content">
                                    <h2 className="text-color-black">September 2019</h2>

                                    <p>
                                        I entered in the Computer Engineering Course at
                                        <a href="https://www.isep.ipp.pt/" aria-label="ISEP" target="_blank"
                                           rel="noreferrer"> ISEP</a> (Instituto
                                        Superior de Engenharia do Porto).
                                    </p>
                                </div>
                            </div>

                            <div className="cd-timeline-block">
                                <div className="cd-timeline-img ">
                                    <img className="img-timeline" src={fiverr} alt=""/>
                                </div>

                                <div className="cd-timeline-content ">
                                    <h2>September 2020</h2>

                                    <p>
                                        I started working as a Freelancer on the

                                        <a href="https://fiverr.com/gjordao " aria-label="Fiverr Platform "
                                           target="_blank "> Fiverr
                                            Platform</a>.
                                    </p>
                                </div>
                            </div>
                        </section>
                    </div>
                </section>
            </div>
            <SpecialFooter/>
        </main>
        </body>
    )
}

export default Timeline;