import SpecialFooter from "../../components/SpecialFooter/SpecialFooter";
import Header from "../../components/Header/header";
import '../../styles/biography.css'

function Biography() {
    return (
        <body className="colorscheme-auto" onLoad="">
        <main className="wrapper">
            <Header/>
            <div className="content">
                <section className="container">
                    <article>
                        <header>
                            <h1>Biography</h1>
                        </header>
                        <i className="fa-calendar calendar-emoji" aria-hidden="true"/>
                        <p className="display-inline">August 7, 2001</p>
                        <i className="fa-map-pin pin-emoji" aria-hidden="true"/>
                        <p className="display-inline">Porto, Portugal</p>
                        <br/><br/>
                        <p>
                            My name is Gonçalo Jordão and I started
                            studying programming in 2019, when I entered in the Computer Engineering course at <a
                            href="https://www.isep.ipp.pt/" aria-label="ISEP" target="_blank" rel="noreferrer">ISEP</a> (Instituto
                            Superior de Engenharia do Porto). After finishing my 1st year of the course I decided to
                            enter the working world (whilst I continued to study), so I started working as a Freelancer
                            on the
                            <a href="https://fiverr.com/gjordao" aria-label="Fiverr Platform" target="_blank" rel="noreferrer"> Fiverr
                                Platform</a>. On that Platform I have done and I continue to do several projects, some
                            simpler others more complex, for people who need it
                            and request it.
                        </p>
                    </article>
                </section>
            </div>
            <SpecialFooter/>
        </main>
        </body>
    )
}

export default Biography;